import React,{Component} from 'react';

class AddTodo extends Component
{
    state=
    {
        text: "",
        id: null

    }

    

    handleChange=(e)=>
    {
        this.setState({
            [e.target.id]:e.target.value
        })
       
    }
    
    handleSubmit=(e)=>
    {
        e.preventDefault();
        console.log(this.state);
        this.props.addTodo(this.state)
        this.setState({
            text:""
        })


    }
    
  render()
  {
      return (
          <div className="add-todo">
            <div className="container-fluid">
             {/* <div className="row justify-content-xs-center">
              <div className="col-xs-5"> */}
              <div className="c-title">
                  new item
              </div>
              <form onSubmit={this.handleSubmit}>
                 <div className="text-input form-group form-control">
                  <input type="text" id="text" name="text" className="input-tag" placeholder="Buy Some Milk" value={this.state.text} onChange={this.handleChange}/>
                  <input type="submit" value="ADD" className="btn a-btn btn-primary"/>
                  </div>
              </form>
              {/* </div>
              </div> */}
            </div> 
          </div>
      )
  }

}
export default AddTodo