import React, { Component } from 'react'

class Todos extends Component 
{
      
    render() {
    const {todos} = this.props;
    const {deleteTodo} =this.props;
    let todosList=<div className="no-todos">
                  <p>checklist empty</p>
                  </div>
                 
   if(todos.length)
  {  
     todosList = todos.map(todo =>{
        console.log("LENGTH",todos.length);   
        return(
            <div className="todo-item row col" key={todo.id}>
               <div className="col-xs-9 text">
                <p>{todo.text}</p>
                </div>
                <div className="col-xs-3 button-col "> 
                <input type="button" className="btn d-btn btn-danger" value="DELETE"  onClick ={ (e)=>{e.preventDefault(); deleteTodo(todo.id)}}/> 
                
                </div>
            </div>
        )
        }
        
    )
    }
    
    return(
        <div className="todos">
          {todosList}
        </div>
    )
}
}

export default Todos