
import React, { Component } from 'react'

import Todos from './todo.js'
import AddTodo from './addTodo.js'
import './App.css';
class App extends Component
{
  state=
  {
    contents:[{id:1,text:"Buy Milk"},{id:2,text:"Buy Rasagulla"}]
    
  }
  deleteTodo=(id)=>
  {
    console.log(id);
    const todos = this.state.contents.filter(content=>{
      return content.id!==id;
    
    })  
    this.setState({
      contents:todos
    })
  }

  addTodo=(todo)=>
  {
    todo.id=Math.random();
    const todos = [...this.state.contents,todo];
    this.setState({
      contents:todos
    });

  }
  render()
  {
    return (
      <div className="app-div">
          <div className="title">
            <p data-aos="fade-down" data-aos-offset="200" data-aos-once="true" data-aos-delay="10" data-aos-duration="2700">Checklist</p>
          </div>
          <div data-aos ="fade-right" data-aos-duration ="2500" data-aos-delay="400"  className="o-cont">
            <div className="container-fluid">
           <Todos todos={this.state.contents} deleteTodo={this.deleteTodo}/>
          </div>
          </div> 
          <AddTodo addTodo={this.addTodo}/>
          <p className="foot">Copyright Â© 2020 manoj ravichandran.</p>
      </div>
    )
  }

}

export default App;
